import Vue from 'vue'
import App from './App.vue'
import router from '../src/router/index.js'
import '../src/style/common.css'
import HeadNav from './views/HeadNav.vue'
import BannerPage from './views/BannerPage.vue'
import AboutPage from './views/AboutPage.vue'
import PricePage from './views/PricePage.vue'
Vue.config.productionTip = false
Vue.component('HeadNav', HeadNav)
Vue.component('BannerPage', BannerPage)
Vue.component('AboutPage', AboutPage)
Vue.component('PricePage', PricePage)
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
