    }
  },
  methods: {
    enter_link (e) {
      console.dir(e.target)
      if (e.target.tagName === 'A') {
        console.log('enter')

        e.target.nextElementSibling.style.width = '100%'
      }
    },
    leave_link (e) {
      if (e.target.tagName === 'A') {
        console.log('leave')

