        e.preventDefault()
        // e.target.nextElementSibling.style.color = 'rgb(36, 147, 216)'
        this.select_index = e.target.nextElementSibling.dataset.id
        console.log(e.target.nextElementSibling.dataset.id)

        // console.log('nihao')
      }
    }
  }
}
</script>

